@mixin Flex {
    display: flex;
    align-items: center;
    justify-content: center;
}

@keyframes block1{
    0%{
        left: -100%;
        opacity: 0;
    }
    100%{
        left: 100%;
    }
}

@keyframes block2{
    0%{
        right: -100%;
        opacity: 0;
    }
    100%{
        right: 100%;
    }
}

@keyframes shape{
    0%{
        left: -100%;
        opacity: 0;
    }
    100%{
        left: 100%;
    }
}

@keyframes link{
    from{
        transform: scale(1) translateY(-50%);
        opacity: 1;
    }
    to{
        transform: scale(0.5) translateY(-50%);
        opacity: 0;
    }
}

@keyframes spin{
    0%{
        transform: rotate(0deg)
    }
    100%{
        transform: rotate(360deg)
    }
}