@import 'main';
// End Of Import

// Preloader
.preloader{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $color-prim;
    @include Flex;
    z-index: 9999;

    .spinner{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        border: 4px solid $color-sec;
        border-left: 4px solid #fec918;
        animation: spin 1s linear infinite;
    }
}

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba($color-sec, 0.8);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 99;
    display: none;

    html[dir="ltr"] & {
        left: 30px;
        right: unset;
    }

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.95);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-prim;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;
    @include Flex;
    flex-direction: column;
    transform-origin: right;
    transform: scaleX(0);

    html[dir="ltr"] & {
        transform-origin: left;
    }

    &.open{
        // transform-origin: left;
        transform: scaleX(1);

        ul{
            opacity: 1;

            li{
                transform: translateX(0);
                opacity: 1;
            }
        }
    }

    &.origin{
        transform-origin: left;
        
        html[dir="ltr"] &{
            transform-origin: right;
        }
    }

    .options {
        padding: 0 50px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        position: absolute;
        top: 50px;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 14px;
            color: #FFF;
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: $color-sec;
        }

        .close1 {
            display: flex;
            font-size: 18px;
            color: $color-sec;
            cursor: pointer;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        opacity: 0;
        transition: .5s;
        transition-delay: .5s;

        & > li{
            position: relative;
            padding: 7px 0;
            // margin-bottom: 20px;
            transition: .5s;
            transform: translateX(10px);
            opacity: 0;

            &:nth-child(1){
                transition-delay: .3s;
            }
            &:nth-child(2){
                transition-delay: .5s;
            }
            &:nth-child(3){
                transition-delay: .7s;
            }
            &:nth-child(4){
                transition-delay: .9s;
            }
            &:nth-child(5){
                transition-delay: 1.1s;
            }
            &:nth-child(6){
                transition-delay: 1.3s;
            }
            &:nth-child(7){
                transition-delay: 1.5s;
            }
            &:nth-child(8){
                transition-delay: 1.7s;
            }
            &:nth-child(9){
                transition-delay: 1.9s;
            }

            a {
                width: 100%;
                display: block;
                font-size: 18px;
                color: $color-sec;
                transition: .5s;
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                html[dir="ltr"] & {
                    left: unset;
                    right: 13px;
                }

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Head

.head{
    background-color: $color-prim;

    .container{
        position: relative;
    }

    .up{
        position: absolute;
        top: 0;
        left: 0;
        width: max-content;
        @include Flex;
        padding-top: 15px;
        padding-left: 15px;

        html[dir="ltr"] & {
            left: unset;
            right: 0;
            padding-top: 15px;
            padding-left: unset;
            padding-right: 15px;
    
        }


        .lang{
            font-size: 12px;
            color: #b58c1c;
            transition: .5s;

            &:hover{
                color: $color-sec;
            }
        }
    
        @include Sm{
            display: none;
        }
    }

    .content{
        padding: 30px 0 23px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Sm{
            flex-direction: column;
        }
    }

    .search{
        width: 260px;
        position: relative;

        @include Sm{
            position: fixed;
            top: 0%;
            left: 0%;
            width: 100%;
            height: 100%;
            background-color: rgba($color-sec, .98);
            z-index: 99;
            @include Flex;
            opacity: 0;
            visibility: hidden;
            transition: .5s;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        .cont{

            i{
                display: none;
            }

            @include Sm{
                width: 70%;
                position: relative;
                top: -15%;

                i{
                    font-size: 24px;
                    color: $color-prim;
                    position: absolute;
                    top: -100px;
                    left: 47%;
                    display: block;
                }
            }
        }

        input{
            width: 100%;
            border: 0;
            border-bottom: 1px solid #B88D1A;
            background-color: transparent;
            height: 40px;
            padding: 10px;
            transition: .5s;
            font-size: 14px;
            color: #b58c1c;

            &::placeholder{
                color: #b58c1c;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%);
            background-color: transparent;
            border: 0;
            padding: 0;

            html[dir="ltr"] & {
                right: 10px;
                left: unset;
            }
            
            svg{

                path{
                    transition: .5s;

                    @include Sm{
                        fill: rgba($color-prim, .6);
                    }
                }
            }

            &:hover{

                svg{

                    path{
                        fill: rgba($color-sec, .6);
                        
                        @include Sm{
                            fill: rgba($color-prim, 1);
                        }
                    }
                }
            }
        }
    }

    .menu{
        margin-top: 25px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-basis: 25%;

        & > div{
            cursor: pointer;

            &:hover{
                svg{

                    path{
                        fill: rgba($color-sec, .6);
                    }
                }
            }

            svg{

                path{
                    transition: .5s;
                    fill: $color-sec;
                }
            }

            &:not(:last-child){

                svg{
                    margin-left: 25px;

                    html[dir="ltr"] & {
                        margin-left: unset;
                        margin-right: 25px;
                    }
                }
            }

            &.user{
                position: relative;
                cursor: pointer;

                &:hover{
                    
                    .drop{
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(0px);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    right: -101%;
                    width: 132px;
                    min-height: 75px;
                    border-radius: 7px;
                    transform: translateY(10px);
                    background-color: rgb(255, 255, 255);
                    text-align: center;                    
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    opacity: 0;
                    visibility: hidden;
                    z-index: 9;
                    transition: .5s;

                    html[dir="ltr"] & {
                        right: -53%;
                    }

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }

                    ul{
                        padding: 10px 13px;

                        li{
                            padding: 0 12px;

                            a{
                                color: $color-sec;
                                transition: .5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-prim;
                                }
                            }

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                transition: .5s;
                            }
                        }
                    }
                }
            }

            &.cart{
                position: relative;

                span{
                    position: absolute;
                    top: 50%;
                    right: 50%;
                    transform: translate(45%, -35%);
                    color: $color-sec;

                    @include Sm{
                        top: 47%;
                        right: 25%;

                        html[dir="ltr"] & {
                            right: 73%;
                        }
                    }
                }

                @include Md{

                    svg{
                        margin-left: 0;

                        html[dir="ltr"] & {
                            margin-right: 0;
                        }
                    }
                }
            }

            &.searBtn{
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }
    }
}

// Head

// Navbar

.navBar{
    background-color: #4F322C;
    height: 50px;
    @include Flex;

    @include Sm{
        display: none;
    }

    & > ul{
        display: flex;
        align-items: center;
        justify-content: center;

        & > li{
            position: relative;

            &:not(:last-child){
                margin-left: 22px;

                html[dir="ltr"] & {
                    margin-left: 0;
                    margin-right: 22px;
                }
            }

            a{
                font-size: 16px;
                color: $color-white;
                transition: .5s;
                
                &:hover{
                    color: $color-prim;
                }
            }
        }
    }
}

// Navbar


// Slider Styles

.owl-nav{
    position: absolute;
    top: 50%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex !important;
    justify-content: space-between;
    align-items: center;

    @include Sm{
        display: none !important;
    }

    button{

        span{
            line-height: 1;
        }
    }
}

.owl-dots{
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs{
        bottom: 15px;
    }
    
    button{

        &:not(:last-child){
            margin-left: 5px;
        }

        &.active{

            span{
                background-color: $color-prim;
            }
        }

        span{
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: rgba($color-sec, 0.6);
        }
    }
}

// Slider Styles

// Main Slider

.main-slider{
    margin-top: 20px;

    .owl-nav{
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        padding: 0 22px;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include Sm{
            display: none;
        }

        button{
            width: 35px;
            height: 35px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.5) !important;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .5s;

            &:hover{
                background-color: $color-prim !important;
            }

            i{
                font-size: 11px;
                color: #fff;
            }
        }
    }

    .owl-dots{
        position: absolute;
        bottom: 30px;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        @include mob600{
            bottom: 10px;
        }
        
        button{

            &:not(:last-child){
                margin-left: 5px;
            }

            &.active{

                span{
                    background-color: $color-white;
                }
            }

            span{
                display: block;
                width: 8px;
                height: 8px;
                background-color: rgba($color-black, 0.5);
                border-radius: 50%;
            }
        }
    }
}

// Main Slider

// Categories

.categories{
    margin: 50px 0 85px;

    @include Sm{
        margin: 50px 0 55px;
    }

    .pic{
        position: relative;
        overflow: hidden;
        width: max-content;
        margin: auto;
        border-radius: 50%;

        img{
            border-radius: 50%;
        }

        &:after, &:before{
            content: "";
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
        }

        &:after{
            left: -100%;
            background-image: linear-gradient(to right, rgba($color-white, 0.1),transparent);
        }
        
        &:before{
            right: -100%;
            background-image: linear-gradient(to right, transparent, rgba($color-white, 0.1));
        }

        &:hover{

            &:after{
                animation: block1 .5s linear;
            }
            &:before{
                animation: block2 .5s linear;
            }
        }
    }

    .title{
        font-size: 18px;
        color: #50332d;
        font-family: "frtBold";
        text-align: center;
        margin-top: 20px;
    }

    .owl-nav{
        width: 102%;
        top: 40%;

        button{
            width: 15px;
            height: 15px;
            border: 1px solid #CFCFCF !important;
            transform: rotate(45deg);
            transition: .5s;

            &:hover{
                border: 1px solid $color-sec !important;
            }
            
            &.owl-prev{
                border-left: 0 !important;
                border-bottom: 0 !important;
            }
    
            &.owl-next{
                border-right: 0 !important;
                border-top: 0 !important;
            }
        }
    }

    .owl-dots{
        display: none !important;

        @include Sm{
            display: flex !important;
            bottom: -30px;
        }
    }

    .owl-carousel .owl-item img{
        width: initial;
        margin: auto;
    }
}

// Categories

// Products

.disc.custom{

    .disc-head{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
    
        & > p{
            font-size: 22px;
            font-family: "frtBold";
            color: $color-sec;
            margin-bottom: 0;

            @include mob375{
                font-size: 20px;
            }
        }
    
        & > a{
            transition: .5s;
            font-size: 16px;
            color: #a5a5a5;

            &:hover{
                color: $color-prim;
            }
        }
    }
    
    .owl-carousel{

        .owl-item{
            display: flex;
            justify-content: center;
        }

        .owl-nav{
            position: absolute;
            top: 39%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 105%;
            height: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 30px;
            margin-top: 0;
            color: #c4c4c4;
            padding: 0;
            right: unset;

            @include Md{
                width: 102%;                
            }

            @include Lg{
                width: 105%;                
            }

            @include Sm{
                display: none;
            }

            button{
                width: 15px;
                height: 15px;
                border: 2px solid #CFCFCF;
                border-right: none;
                border-bottom: none;
                transform: rotate(-45deg);
                border-radius: 1px;
                transition: .5s;

                &.owl-prev{
                    transform: rotate(135deg);
                }

                &:hover{
                    background-color: transparent;
                    border: 2px solid $color-sec;
                    border-right: none;
                    border-bottom: none;
                }

                span{
                    display: none;
                }
            }
        }

        .owl-dots{
            display: none;

            @include Sm{
                display: flex;
                justify-content: center;
                align-items: center;
                position: unset;
                transform: translateY(35px);
            }
        }

        .owl-item{

            img{
                width: initial;
                margin: auto;
            }
        }
    
    }
}

.product{
    width: 100%;
    position: relative;
    text-align: center;
    background-color: transparent;
    overflow: hidden;
    transition: .5s;

    .top{
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $color-prim;
        @include Flex;
        position: absolute;
        top: 10px;
        right: 10px;

        html[dir="ltr"] & {
            right: unset;
            left: 10px;
        }
        
        span{
            font-size: 12px;
            font-family: "frtBold";
            color: #50332d;
            line-height: 1;
        }
    }
    
    .pic{
        height: 235px;
        background-color: $color-white;
        border: 1px solid #F1F1F1;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .detail{
        padding: 20px 0 0;
        background-color: transparent;
    }

    & > a{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    & > img{
        width: initial;
        height: 167px;
        display: block;
        margin: auto;
    }

    .pro-title{
        font-size: 14px;
        color: #50332d;
        transition: .5s;
    }

    &:hover{
        .pro-title{
            color: $color-prim;
        }
    }

    span{
        font-size: 16px;
        font-family: "frtBold";
        display: inline-block;
        direction: ltr;

        &.price{
            color: #ff0000;
            margin-right: 8px;
        }

        &.discount{
            color: #50332d;
            text-decoration: line-through;
        }
    }
}

// Products

// Banners

.banners{
    margin: 70px 0;

    @include Sm{
        margin-bottom: 70px 0 40px;
    }

    .col-sm-6{

        &:first-child{
            margin-bottom: 30px;
        }
    }

    a{
        display: block;
        overflow: hidden;
        position: relative;

        &:after{
            content: "";
            position: absolute;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to right, transparent, rgba($color-white, .3));
        }

        img{
            transition: .5s;
            margin: auto;
        }
        
        &:hover{
            
            img{
                transform: scale(1.03);
            }

            &:after{
                animation: shape .5s linear;
            }
        }
    }
}

// Banners

// footer

footer{
    margin-top: 115px;
    padding: 58px 0;
    padding-bottom: 0;
    background-color: #4F322C;

    @include Sm{
        padding: 40px 0 20px;
        margin-top: 75px;
    }

    .foot-links{

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 15px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;

                html[dir="ltr"] & {
