                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 14px;
            font-family: "frt";
            backface-visibility: hidden;
            color: #998986;
            position: relative;
            -webkit-background-clip: text;
