            -webkit-text-fill-color: transparent;
            background-image: linear-gradient(to right, $color-prim, $color-prim 50%, #998986 50%);
            background-size: 200% 100%;
            background-position: 100%;
            display: inline-block;
            
            // html[dir="ltr"] & {
            //     background-size: 200% 100%;
            //     background-position: 0%;
            //     background-image: linear-gradient(to left, $color-prim, $color-prim 50%, #998986 50%);
            // }

            &:hover{
                transition: all 0.5s cubic-bezier(0.000, 0.000, 0.230, 1);
                background-position: 0%;
                
                // html[dir="ltr"] & {
                //     background-position: -100%;
                // }
            }
        }
    }

    .newsletter{
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0;
            text-align: right;

            html[dir="ltr"] & {
                text-align: left;
            }
        }

        @include Sm{
            margin-top: 35px;
            text-align: center;
        }

        & > p{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 20px;
        }
    }

    .frm{
        position: relative;
        margin-bottom: 17px;
        width: 360px;

        @include Md{
            margin: auto;
            margin-bottom: 17px;
        }

        @include Sm{
            margin: auto;
            margin-bottom: 17px;
        }

        @include mob375{
            width: 280px;
        }

        input{
            padding: 0 15px;
            height: 40px;
            width: 100%;
            border: 0;
            border-bottom: 1px solid #69504B;
            color: #836e6a;
            font-size: 14px;
            background-color: transparent;
            transition: .5s;

            &::placeholder{
                color: #836e6a;
            }

            &:focus{
                border-bottom: 1px solid $color-prim;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 5px;
            transform: translateY(-50%);
            width: 37px;
            height: 30px;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: transparent;

            html[dir="ltr"] & {
                right: 5px;
                left: unset;
            }

            &:hover{

                svg{
                    path{
                        fill: $color-prim
                    }
                }
            }

                
            svg{
                path{
                    transition: .5s;
                }
            }
        }
    }

    .more{
        margin: 35px 0;

        @include Flex;
        justify-content: space-between;

        @include Sm{
            flex-direction: column;
            margin: 10px 0;
        }

        .download{
            @include Flex;
            justify-content: flex-start;

            @include Sm{
                margin: 20px 0;
            }

            @include mob480{
                flex-wrap: wrap;
                justify-content: center;
            }

            p{
                font-size: 16px;
                font-family: "frtBold";
                color: #fff;
                margin: 0;
                margin-left: 12px;

                @include mob480{
                    width: 100%;
                    margin-right: 0;
                    margin-left: 0;
                    text-align: center;
                    margin-bottom: 15px;
                }
    
                html[dir="ltr"] & {
                    margin-right: 12px;
                    margin-left: unset;
                    font-size: 14px;
                }
            }

            a{
                display: inline-block;
                transition: .5s;

                &:hover{
                    transform: translateY(-3px);
                }

                &:first-of-type{
                    margin-left: 4px;
                    
                    html[dir="ltr"] & {
                        margin-right: 4px;
                        margin-left: 0;
                    }
                }
            }
        }

        .follow{
            display: block;
    
            @include Sm{
                margin-top: 0px;
                margin-bottom: 0px;
            }
    
            @include Flex;
            justify-content: flex-start;
            color: #fff;
    
            @include Sm{
                justify-content: center;
            }
    
            @include mob480{
                flex-wrap: wrap;
            }
    
            *{
                color: inherit;
            }
    
            p{
                font-size: 16px;
                font-family: "frtBold";
                margin: 0;
                margin-left: 12px;
    
                html[dir="ltr"] & {
                    margin-right: 12px;
                    margin-left: unset;
                    font-size: 14px;
                }
    
                @include mob480{
                    width: 100%;
                    margin-right: 0;
                    margin-left: 0;
                    text-align: center;
                    margin-bottom: 15px;
                }
            }
    
            a{
                position: relative;
                background-color: #63443D;
                border-radius: 50%;
                @include Flex;
                transition: .5s;
    
    
                &:after{
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) scale(0);
                    width: 85%;
                    height: 85%;
                    border-radius: 50%;
                    transition: .5s;
                    background-color: $color-prim;
                    z-index: 1;
                }
    
                i{
                    transition: .5s;
                    position: relative;
                    z-index: 2;
                    font-size: 18px;
                }
    
                &:not(:last-child){
                    margin-left: 8px;
                    
                    html[dir="ltr"] & {
                        margin-left: unset;
                        margin-right: 6px;
                    }
                }
    
                &:hover{
    
                    &:after{
                        transform: translate(-50%, -50%) scale(1);
                    }
    
                    i{
                        color: $color-sec;
                    }
                }
    
                @include Md{
                    width: 30px;
                    height: 30px;
    
                    i{
                        font-size: 16px;
                    }
    
                    &:not(:last-child){
                        margin-left: 6px;
                        
                        html[dir="ltr"] & {
                            margin-left: unset;
                            margin-right: 6px;
                        }
                    }
                }
    
                @include Lg{
                    width: 40px;
                    height: 40px;
    
                    i{
                        font-size: 18px;
                    }
    
                    &:not(:last-child){
                        margin-left: 8px;
                        
                        html[dir="ltr"] & {
                            margin-left: unset;
                            margin-right: 8px;
                        }
                    }
                }
    
                @include Sm{
                    width: 40px;
                    height: 40px;
    
                    i{
                        font-size: 18px;
                    }
    
                    &:not(:last-child){
                        margin-left: 8px;
                        
                        html[dir="ltr"] & {
                            margin-left: unset;
                            margin-right: 8px;
                        }
                    }
                }
            }
        }
    }

}

// End Of Footer

// End

